@import '../../../includes/assets/css/consts.scss';


.dpit-wrapper {
	--dp_unit: 0.51px;
	--accent-color: 79, 53, 167;
	--accent-contrast-color: 255, 255, 255;
	--accent-contrast-color-gentle: 150, 150, 150;
	--highlight-layer-bg-color: var(--accent-contrast-color);
	--highlight-layer-bg-color-lighter: 255, 255, 255;
	--highlight-layer-bg-color-darker: 34, 34, 34;

	--tooltip-border-radius: 2px;
	--num-size-coef: 1;
	--highlight-border-radius: 2px;
	--button-border-radius: 2px;

	--bullet-anim-size: 5vw;
	--bullet-anim-gap: 0.8vw;
	--bullet-anim-border-radius: 4px;

	--btn_font: inherit;


	@include atXXXS {
		--dp_unit: 0.11vw !important;
	}
}



.dpit-wrapper {
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	pointer-events: none;
	box-sizing: border-box;
	//overflow-x: hidden;
	//overflow-y: hidden;
}

.dpit-main {
	position: relative;
	width: 100%;
	height: 100%;
}

.dpit-overlay-container {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	min-height: 120vh;
	min-height: calc(var(--dpit-vh, 1vh) * 120);
	height: 100%;
	@include z-index;
	pointer-events: none;
	@include transition(opacity, $transition-time-slow, false, ease-in);
}

.dpit-overlay {
	width: 100%;
	height: 100%;
	background-color: rgb(var(--accent-contrast-inv-color));
	transition: background-color $transition-time-med linear, opacity $transition-time-med linear;
	pointer-events: all;

	&--blurred {
		background-color: rgba(var(--accent-contrast-inv-color), 0.7);
		-webkit-backdrop-filter: blur(4px);
		backdrop-filter: blur(4px);
	}
}

.dpit-target-parent {
	z-index: auto !important;
	filter: none !important;
	mix-blend-mode: unset !important;

	&--fixed,
	&--fixed-child {
		@include z-index(2, true);
		pointer-events: none !important;
	}
}


.dpit-target,
tr.dpit-target>td,
tr.dpit-target>th {
	@include z-index(2, true);
	opacity: 1 !important;
	mix-blend-mode: unset !important;
	pointer-events: all;
}

.dpit-target {
	&--click {
		cursor: pointer !important;
		transition: opacity $transition-time-fast ease-in-out !important;
	}

	&--hover {
		transition: opacity $transition-time-fast ease-in-out !important;
	}

	&--block-events {
		pointer-events: none !important;
	}

}


.dpit-disable-interaction {
	@include z-index(3, true);
	position: absolute;
	opacity: 0;
	pointer-events: all;
}

.dpit-relative-position,
tr.dpit-target>td,
tr.dpit-target>th {
	position: relative;
}

.dpit-highlight {
	box-sizing: border-box;
	position: absolute;
	@include z-index(1);
	background-color: rgb(var(--accent-contrast-color));
	border: 4px solid rgb(var(--accent-contrast-color));
	border-radius: var(--highlight-border-radius);
	@include transitionFast(opacity);
	max-width: 100%;
	pointer-events: none;

	@include atS {
		border: 3px solid rgb(var(--accent-contrast-color));
	}

	@include atXS {
		border: 2px solid rgb(var(--accent-contrast-color));
	}

	&.dpit-no-lradius {
		border-top-left-radius: 0;
		border-bottom-left-radius: 0;
	}

	&.dpit-no-rradius {
		border-top-right-radius: 0;
		border-bottom-right-radius: 0;
	}



	&.light {
		background-color: rgb(var(--highlight-layer-bg-color-lighter));
	}

	&.dark {
		background-color: rgb(var(--highlight-layer-bg-color-darker));
	}



	&--fixed-pos {
		@include z-index(3);
		background-color: transparent !important;
		outline: 3px solid rgb(var(--accent-contrast-color));
		border: 3px solid rgb(var(--accent-color));
	}

	&.highlight-dummy {
		background-color: transparent !important;
		border: none;
	}
}

.dpit-tooltip-ref {
	position: absolute;
	visibility: hidden;
	@include z-index(4);
	background-color: transparent;
	@include transitionFast(opacity);
	//border: 4px solid transparent;
}

.dpit-tooltip {
	position: absolute;
	visibility: visible;
	padding: unit(35) unit(50) unit(20);
	background-color: rgb(var(--accent-contrast-color));
	min-width: unit(750);
	max-width: calc(95vw - unit(80));
	border-radius: var(--tooltip-border-radius);

	.dpit-tooltip-text {
		color: rgb(var(--accent-contrast-inv-color));
		white-space: pre-wrap;
		overflow-wrap: break-word;
		margin-bottom: var(--p_mb);

		&--rt {
			white-space: normal;
		}

		&--cs {
			@import './../../../includes/assets/css/editor-content';

			a {
				color: rgb(var(--accent-color))
			}
		}

		@import './../../../includes/assets/css/editor-content-color';

		*:last-child {
			margin-bottom: 0 !important;
		}

		img.alignright {
			float: right;
			margin: 0 0 1em 1em
		}

		img.alignleft {
			float: left;
			margin: 0 1em 1em 0
		}

		img.aligncenter {
			display: block;
			margin-left: auto;
			margin-right: auto
		}

		a img.alignright {
			float: right;
			margin: 0 0 1em 1em
		}

		a img.alignleft {
			float: left;
			margin: 0 1em 1em 0
		}

		a img.aligncenter {
			display: block;
			margin-left: auto;
			margin-right: auto
		}

		pre {
			white-space: pre-wrap;
		}

		&::after {
			content: "";
			clear: both;
			display: table;
		}
	}

	@include atXXXS {
		max-width: calc(95vw - unit(20));
	}

	&--center {
		background-color: rgba(var(--accent-contrast-color), 0.97);
		box-shadow: 1px 0 5px 2px rgba(var(--accent-contrast-color), 0.21);
	}
}

.dpit-tooltip-ref.dpit-fixed {
	.dpit-tooltip {


		.dpit-tooltip-text {
			max-height: calc(90vh - unit(200));
			overflow-y: auto;
		}
	}
}

.dpit-tooltip-number {
	box-sizing: content-box;
	position: absolute;
	visibility: visible;
	display: flex;
	justify-content: center;
	align-items: center;
	top: 0px;
	left: 0px;
	@include z-index(20, true);
	font-size: unit(calc(35 * var(--num-size-coef)));
	font-weight: 600;
	color: rgb(var(--accent-contrast-color));
	text-align: center;
	background-color: rgb(var(--accent-color));
	width: unit(calc(82 * var(--num-size-coef)));
	height: unit(calc(82 * var(--num-size-coef)));
	line-height: 1;
	border-radius: 50%;
	border: 3px solid rgba(var(--accent-contrast-color), 0.9);

	&--over-right {
		border-top-right-radius: 0;
		border-bottom-right-radius: 0;
		border-right-width: 0;
	}

	&--over-left {
		border-top-left-radius: 0;
		border-bottom-left-radius: 0;
		border-left-width: 0;
	}

	&--ba {
		border-bottom-left-radius: 0;
		border-bottom-right-radius: 0;
		border-bottom-width: 0;
	}

	&--ta {
		border-top-left-radius: 0;
		border-top-right-radius: 0;
		border-top-width: 0;
	}


	@include atS {
		width: unit(calc(75 * var(--num-size-coef)));
		height: unit(calc(75 * var(--num-size-coef)));
		font-size: unit(calc(32 * var(--num-size-coef)));
	}
}

.dpit-arrow {
	border: unit(35) solid transparent;
	position: absolute;
	visibility: visible;
	@include z-index(4, true);

	&--top {
		border-bottom-color: rgb(var(--accent-contrast-color));
	}

	&--right {
		border-left-color: rgb(var(--accent-contrast-color));
	}

	&--bottom {
		border-top-color: rgb(var(--accent-contrast-color));
	}

	&--left {
		border-right-color: rgb(var(--accent-contrast-color));
	}
}

.dpit-tooltip.disabled {
	pointer-events: none;
}

.dpit-tooltip-buttons {
	padding-top: unit(30);
	padding-bottom: unit(7);
	display: flex;
	flex-wrap: nowrap;
	justify-content: flex-end;
}

.dpit-button {
	position: relative;
	overflow: visible;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: unit(23) unit(33);
	margin: 0;
	color: rgba(var(--accent-contrast-inv-color), 0.9);
	font-size: unit(35);
	font-weight: 400;
	line-height: 1.5;
	white-space: nowrap;
	cursor: pointer;
	-webkit-user-select: none;
	user-select: none;
	@include transitionMulti($transition-time-faster-x, linear, background-color, color, text-shadow);
	border-radius: var(--button-border-radius);
	background-color: transparent;
	outline: none;


	@include atXXXS {
		letter-spacing: normal;
		min-width: unset;
	}

	&__spacer {
		content: '';
		min-width: unit(25);
	}

	&__ico {
		display: block;
		width: unit(32);
	}

	&--skip {
		margin-right: auto;
	}

	&--next {
		border-radius: 0 var(--button-border-radius) var(--button-border-radius) 0;

		.dpit-button__ico {
			transform: rotate(180deg);
			margin-left: unit(10);
		}

		.dpit-button__text-ico {
			margin-left: 5px;
		}
	}

	&--prev {
		border-radius: var(--button-border-radius) 0 0 var(--button-border-radius);
		margin-left: unit(30);

		.dpit-button__ico {
			margin-right: unit(10);
		}

		.dpit-button__text-ico {
			margin-right: 5px;
		}
	}

	&--full {
		margin-right: unit(0);
		border-radius: var(--button-border-radius);
	}

	&--prev {
		border-radius: var(--button-border-radius) 0 0 var(--button-border-radius);
		margin-left: unit(30);
	}
}

.dpit-bullets {
	text-align: center;
	padding: unit(12) 0 unit(3);
	-webkit-user-select: none;
	user-select: none;
	overflow-y: visible;

	&--pointer-events-none {
		pointer-events: none;
	}

	&--hidden {
		opacity: 0 !important;
		pointer-events: none !important;
	}

	ul {
		margin: 0 auto;
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 0;
		flex-wrap: nowrap;
		overflow: auto visible;
		-webkit-overflow-scrolling: touch;
		direction: ltr;

		&.dpit-has-scrollbar {
			margin: 0;
			padding-bottom: unit(10);
			justify-content: flex-start;
		}

		@media (hover: hover) {
			&::-webkit-scrollbar {
				height: unit(13);
			}

			&::-webkit-scrollbar-track {
				border-radius: var(--button-border-radius);
				background-color: rgba(var(--accent-contrast-inv-color), 0.1);
			}

			&::-webkit-scrollbar-thumb {
				border-radius: var(--button-border-radius);
				background-color: rgba(var(--accent-contrast-inv-color), 0.6);
			}
		}


		li {
			list-style: none;
			margin: unit(8) unit(5);
			width: unit(22);
			height: unit(22);
			cursor: pointer;
			border-radius: 50%;
			text-decoration: none;
			outline: none;
			transform-origin: center;
			overflow: hidden;
			display: block;
			flex-shrink: 0;
			background-color: rgba(var(--accent-color), 0.336);
			@include transitionMultiFasterX(transform, background-color);
			//@include transitionFast(transform);

			//step number
			>div {
				display: none;
			}

			&:hover {
				@include atHoverAbleDev {
					transform: scale(1.03);
					background-color: rgba(var(--accent-color), 0.737);
				}
			}

			&:active,
			&.active {
				cursor: default;
				background-color: rgba(var(--accent-color), 0.737);
				transform: scale(1.03);
			}

			&.not-visible-ref {
				opacity: 0.4;
				cursor: default;
			}

		}
	}
}

// on touch devices - big bullets with number
.dpit-wrapper:not(.dpit-is-sticky-theme) {
	.dpit-bullets {

		@include atTouchDev {
			padding: unit(12) 0 0;
		}

		ul {
			padding-bottom: unit(15);

			li {
				@include atTouchDev {
					width: unit(50);
					height: unit(50);
					margin: unit(20) unit(8);
					display: flex;
					justify-content: center;
					align-items: center;
					@include transitionFast(transform, box-shadow);
				}

				//step number
				>div {
					@include atTouchDev {
						line-height: 1;
						font-weight: 600;
						//@include transitionFast(font-size);
						display: flex;
						flex-shrink: 1;
						font-size: unit(20);
						color: rgba(var(--accent-contrast-inv-color), 0.95);
					}
				}

				&:active,
				&.active {
					@include atTouchDev {
						transform: scale(1.3);
						background-color: rgb(var(--accent-color), 0.95);
						margin-left: unit(10);
						margin-right: unit(10);
					}

					>div {
						@include atTouchDev {
							font-size: unit(25);
							color: rgba(var(--accent-contrast-color), 0.95);
						}
					}
				}
			}
		}
	}
}


.dpit-bullet-pulse {
	animation: bullet-pulse 200ms;
}


.dpit-gelatine-pulse {
	animation: gelatine 0.5s;
}

@keyframes gelatine {

	from,
	to {
		transform: scale(1, 1);
	}

	25% {
		transform: scale(0.95, 1.05);
	}

	50% {
		transform: scale(1.05, 0.95);
	}

	75% {
		transform: scale(0.975, 1.025);
	}
}


@keyframes bullet-pulse {
	0% {
		box-shadow: 0 0 2px 2px rgba(var(--accent-color), 0.2);
		transform: scale(1.2);
	}

	70% {

		box-shadow: 0 0 2px 2px rgba(var(--accent-color), 0.4);
		transform: scale(1.35);
	}

	100% {
		box-shadow: 0 0 2px 2px rgba(var(--accent-color), 0.2);
		transform: scale(1.3);
	}
}


.dpit-rb-anim {
	position: fixed;
	@include z-index(2, true);
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	margin: 0;
	transition: opacity $transition-time-med ease-out;
	padding: var(--bullet-anim-gap);

	&.inactive {
		opacity: 0 !important;
	}

	ul {
		margin: 0 auto;
		padding: 0;
		display: flex;
		justify-content: center;
		overflow: visible;

		li {
			list-style: none;
			border-radius: var(--bullet-anim-border-radius);
			width: var(--bullet-anim-size);
			height: var(--bullet-anim-size);
			margin: 0 var(--bullet-anim-gap);
			transition: 0.4s ease-out, background-color 0.185s ease-out;
			transform-origin: center;
			pointer-events: none;
			background-color: rgba(var(--accent-color-gentle), 0.9);
			position: relative;
			overflow: hidden;
			@include z-index(1);

			&.active {
				transform: scale(2.2);
				background-color: rgba(var(--accent-contrast-color), 1);
				@include z-index(4);
			}

			&.semi-active {
				transform: scale(1.2);
				background-color: rgba(var(--accent-contrast-color), 0.8);
				@include z-index(3);

				&--2 {
					transform: scale(1.1);
					background-color: rgba(var(--accent-contrast-color), 0.5);
					@include z-index(2);
				}
			}

		}
	}



	&--in-highlight {
		position: relative;
		pointer-events: none;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		max-width: 95%;

		ul {
			li {
				width: unit(40);
				height: unit(40);
				max-width: 25%;
				margin: unit(20) 4%;
			}
		}
	}
}

.dpit-no-trans {
	transition: unset !important;
}

.dpit-pulse {
	animation: pulse 1s;
}

.dpit-progress {
	overflow: hidden;
	height: unit(20);
	margin: unit(30) unit(5) unit(10);
	border-radius: var(--button-border-radius);
	background-color: rgba(var(--accent-color), 0.2);
}

.dpit-progress-bar {
	@include transitionFast(width);
	width: 0%;
	height: 100%;
	background-color: rgba(var(--accent-color), 0.822);
}

.dpit-target-dummy {
	position: fixed;
	height: 100%;
	width: 100%;
	left: 0;
	top: 0;
	min-height: 100vh;
	min-height: calc(var(--dpit-vh, 1vh) * 100);
}

.dpit-disabled {
	pointer-events: none;
	opacity: 0.3 !important;
	/*color: rgba(136, 136, 136, 0.514);
	background-color: rgba(var(--accent-color), 0.563);
	box-shadow: none;*/
	cursor: default;
}

.dpit-fixed {
	position: fixed;
}

.dpit-hidden {
	opacity: 0 !important;
	pointer-events: none !important;

	&--no-space {
		width: 0 !important;
		height: 0 !important;
		padding: 0 !important;
		margin: 0 !important;
	}

	&--by-disp {
		display: none !important;
	}
}

.dpit-hidden-by-disp {
	display: none !important;
}



.dpit-hidden-by-opacity {
	opacity: 0 !important;
	width: 0 !important;
	//height: 0 !important;
	pointer-events: none !important;
	padding: 0 !important;
	margin: 0 !important;
}

.dpit-hidden-at-start {
	pointer-events: none !important;
	z-index: -1 !important;
}


.dpit-wrapper.dpit-light-accent {

	.dpit-overlay {
		background-color: rgba(var(--accent-contrast-inv-color), 0.8);

		&--blurred {
			background-color: rgba(var(--accent-contrast-inv-color), 0.6);
		}
	}

	.dpit-rb-anim {
		ul {
			li {
				background-color: rgb(var(--accent-contrast-color));

				&.active {
					background-color: rgb(var(--accent-color));
				}

				&.semi-active {
					background-color: rgb(var(--accent-color));
				}
			}
		}
	}

	.dpit-tooltip-number {

		&--on-tooltip {
			border-color: rgba(var(--accent-contrast-color), 0.95);
		}
	}
}



.dpit-on #wpadminbar:not(.dpit-target-parent) {
	@include z-index(-1, true);

	@media screen and (max-width: 782px) {
		position: static !important;
		overflow: hidden;

	}
}

html.dpit-on {
	@media screen and (max-width: 782px) {
		margin-top: 0 !important;
	}
}

html.dpit-on,
html.dpit-on body.wp-admin {
	// body and html at wp-admin - height 100% fix (chrome make 100% to window.height instead of doc.height)
	height: unset;
}

html.dpit-on body {
	--dp-z-index-base: 2147483600;
}